import React from 'react';
import CloudTitle from './CloudTitle';
import LittleButton from './LittleButton';
import './Recents.css';

class Recents extends React.Component {

    constructor(props) {
        super(props);
        this.state = {
          repos: null
        };
      }

    componentDidMount() {
        // TODO: Read from TacoCloud API
        //
        // ANGULAR CODE:
        // this.httpClient.get('http://localhost:8080/design/recent')
        //     .subscribe(data => this.recentTacos = data);


        const apiUrl = 'https://api.github.com/users/hacktivist123/repos';
        fetch(apiUrl)
          .then((response) => response.json())
          .then((data) => {
                console.log('This is your data', data);
                this.setState({ repos: data});
                // setAppState({ repos: data });
          });
    }

    render() {
        return (
            <div>
                <CloudTitle>Admire some recently created taco masterpieces</CloudTitle>

                <p>Even the greatest artists sometimes need to be inspired by the works of others.
                Here are some of the most recently created tacos, designed by taco artists just like you...</p>

                <div className="recentsblock">
                    {/* <div className="tacoblock" *ngFor="let taco of recentTacos"> */}
                    { (this.state.repos ? this.state.repos : []).map((repo) => {
                        return (

                    <div className="tacoblock">
                        <div className="tacoName"><span className="small">The</span><br/>"{repo.name}"</div>
                        <div className="tacoDesc">
                            <ul className="commalist">
                                {/* <li *ngFor="let ingredient of taco.ingredients | nonwraps">{{ingredient.name}}</li> */}
                                <li>ingredient.name</li>
                            </ul>
                            {/* <div *ngIf="(taco.ingredients | wraps)?.length > 0"> */}
                            <div>
                                <span>wrapped in</span>
                                <ul className="commalist">
                                    <li>ingredient.name</li>
                                </ul>
                            </div>
                            {/* <div *ngIf="(taco.ingredients | wraps)?.length == 0">in a bowl</div> */}
                            <div>in a bowl</div>
                        </div>
                        <LittleButton label="Order this taco"></LittleButton>
                    </div>
                        );
                    })};

                </div>
            </div>
        );
    }

}

export default Recents;