import React from 'react';
import './GroupBox.css';

class GroupBox extends React.Component {

    render() {
        return (
            <div class="groupBox">
                <div class="groupBoxTitle">{this.props.title}</div>
                <div class="groupBoxContent">
                    {this.props.children}
                </div>
            </div>
        );
    }

}

export default GroupBox;