import React from 'react';
import TacoHeader from './TacoHeader';
import TacoFooter from './TacoFooter';
import Home from './Home';
import Login from './Login';
import Design from './Design';
import Locations from './Locations';
import Recents from './Recents';
import Specials from './Specials';
import { HashRouter as Router, Route } from "react-router-dom";
import './TacoCloud.css';
import Cart from './Cart';

class TacoCloud extends React.Component {

    render() {
        return (

                <div className="content">
            <div>
                <TacoHeader />

                <Router>
                    <Route exact path="/" component={Home}></Route>
                    <Route exact path="/login" component={Login}></Route>
                    <Route exact path="/cart" component={Cart}></Route>
                    <Route exact path="/recents" component={Recents}></Route>
                    <Route exact path="/specials" component={Specials}></Route>
                    <Route exact path="/locations" component={Locations}></Route>
                    <Route exact path="/design" component={Design}></Route>
                </Router>

                <TacoFooter />
            </div>
                </div>

        );
    }

}

export default TacoCloud;